}

.feedback__label_required::after {
  content: "*";
  color: #EF4D9C;
}

.feedback__label.active {
  opacity: 1;
}

.feedback__item {
  position: relative;
  width: 100%;
  height: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #565658;
