  left: 0;
  height: 100%;
  width: 100%;
}

.feedback__placeholder {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  color: #959595;
}

.feedback__placeholder.active {
  color: transparent;
}

.feedback__placeholder.active span {
  color: transparent;
}

.feedback__placeholder span {
  color: #EF4D9C;
}

.feedback__agreement {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.feedback__checkbox {
  display: none;
}

.feedback__checkbox:checked + .feedback__castom-checkbox .feedback__check {
  display: block;
}

.feedback__castom-checkbox {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
