  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.03em;
  cursor: default;
}

.feedback__note::before {
  content: "* ";
  color: #EF4D9C;
}

.feedback__ticket {
  position: absolute;
  background-color: #F9931F;
  width: 48px;
  color: #FFF;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.feedback__ticket_max {
  z-index: 51;
  top: 25%;
  left: -48px;
  height: 188px;
